import React, { useState, useEffect } from "react";
import { motion } from "framer-motion";
import profilepic from "../images/profilepic.jpg";
import "./Intro.css";

function Intro() {
  const [offsetY, setOffsetY] = useState(0);

  const icon = {
    hidden: {
      pathLength: 0,
      fill: "rgba(255, 255, 255, 0)",
    },
    visible: {
      pathLength: 1,
      fill: "rgba(255, 255, 255, 1)",
    },
  };

  const handleScroll = () => setOffsetY(window.pageYOffset);

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);

    return () => window.removeEventListener("scroll", handleScroll);
  }, []);
  return (
    <div className="Intro">
      <div class="Intro__card">
        <img
          className="Intro__profilepic"
          src={profilepic}
          style={{ backgroundSize: "cover" }}
          alt=""
        />
        <div className="Intro__written">
          <ul>
            My name is Nischal Tripathi, I'm currently persuing Bachelor's of
            Computer Science from The University of Adelaide. I live in
            Adelaide, South Australia. I completed my schooling from St.
            Xavier's Sr. Sec. CO-ED School, Bhopal, India. I got 100% in my
            final Computer Science practicals I was also HOD of Networking
            department in my School's Computer Club. I've a great knowledge of
            multiple web development tools like HTML, CSS, PHP, JavaScript,
            ReactJS, NextJS, VueJS, NodeJS, WordPress, etc. I've completed many
            personal projects using these skills and looking for an Internship
            or an entry level job to impore myself.
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Intro;
