import React from "react";
import "./DownloadCV.css";

const DownloadCV = () => {
  return (
    <div className="downloadCV">
      <div
        onClick={() =>
          window.open(
            "https://drive.google.com/file/d/1KtzjuSKT_6xE6_JJqquvfcaaKPYLpKXA/view?usp=sharing",
            "_blank"
          )
        }
        className="downloadCV__button"
      >
        Download CV
      </div>
    </div>
  );
};

export default DownloadCV;
